<template>
	<div class="panelAddNotes">
		<textarea v-model="content" class="textarea" rows="20" placeholder="请输入内容！"></textarea>
		{{content}} 
	</div>
</template>
<style scoped>
	.textarea{
		width: 100%;
		padding: 20px;
		box-sizing: border-box;
	}
</style>
<script>
	import { mapState } from 'vuex'
	export default{
		data(){
			return{
				content:""
			}
		},
		beforeRouteLeave(to,form,next){
			this.addNotes()
			next()
		},
		methods:{
			addNotes(){
				if(this.content==''){
					return
				}
				if(this.$route.query.id){//编辑
					let index=this.$store.state.notes.findIndex(panel=>panel.id==this.$route.query.id)
					this.$set(this.$store.state.notes[index],'content',this.content)
					let data={
						id:this.$store.state.notes[index].id,
						content:this.content,
						isTop:this.$store.state.notes[index].isTop,
						date:this.$store.state.notes[index].date
					};
					localStorage.setItem('notes',JSON.stringify(this.$store.state.notes))
				}else{//新增
					let data={
						id:this.$store.state.notesID+1,
						content:this.content,
						isTop:false,
						date:new Date().getTime()
					};
					this.$store.dispatch('saveNotesID',data.id)
					this.$store.dispatch('addNotes',data)
				}
				
				
			}
		},
		mounted(){
			if(this.$route.query.id){
				this.content=this.$store.state.notesEditData.content;
			}
		}
	}
</script>


